    font-family: "m6x11";
    font-size: 24px;

    text-align: center;
    color: #f6d6bd;

    height: 100%;
    -webkit-transform: translateY(45%);
            transform: translateY(45%);
}

.work-img {
    position: absolute;
    z-index: 2;

    border: 2px solid #f6d6bd;
    padding: 2px;
    border-radius: 5px;

    width: 200px;
    height: 200px;
}

.work-img-cover {
    position: absolute;
    z-index: 2;

    border: 2px solid #f6d6bd;
    padding: 2px;
    border-radius: 5px;

    opacity: 1;

    transition: 0.3s;
    -webkit-transition: 0.3s;

