    height: 200px;
}

.work-img-cover.hover {
    border: 2px solid #f6d6bd;
    padding: 2px;
    border-radius: 5px;

    opacity: 0;

    transition: 0.3s;
    -webkit-transition: 0.3s;
}

.work-gif {
